@use 'base/variables' as v; 

.testimonio{
    background-color: lighten(v.$verde, 4);
    font-size: 2.4rem;
    padding: 2rem;
    color: v.$blanco;
    border-radius: 2rem;
    box-shadow: 0px 4px 10px rgb(77, 77, 77);

   
    blockquote{
        margin: 0 1rem;
        padding-left: 4rem;
        position: relative;
        &::before{
            content: "";
            background-image: url(/build/img/comilla.svg);
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left: -2rem;
        }
        // &::after{
        //     content: "";
        //     background-image: url(/build/img/comilla.svg);
        //     background-repeat: no-repeat;
        //     width: 6rem;
        //     height: 8rem;
        //     display: block;
        //     position: absolute;
        //     right: 1rem;
        //     bottom: -1rem;
        //     transform: rotate(180deg);
        // }
    }
    p{
        text-align: right;
        color: v.$blanco;
        font-weight: bold;
        font-style: italic;
    }

}