@use 'base/variables' as v; 
@use 'base/mixins' as m; 


    .dark-mode-boton{
        width: 3.5rem;
        filter: invert(100%);
        &:hover{
            cursor: pointer;
        }

    }
    @media (max-width: 48rem) {
        .barra img{
            margin: 0 auto;
        }
    }
    @media (min-width:48.1rem){
        .barra img{
        margin-left:auto;
        }
    }

  

.barra{
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include m.tablet {
        flex-direction:row; 
        

    }

    .logo{
        width: 30rem;
    }
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;

        
    }
}

.navegacion{
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .0s ease-in-out, opacity .5s linear, height .5s linear;


    @include m.tablet {
        display:flex;
        height: auto;
        align-items: center;
        visibility: visible;
        opacity: 1;

        
    }
    a{
        color:v.$blanco;
        text-decoration: none;
        font-size: 2rem;
        display: block;
        text-align: center;
        @include m.tablet {
            font-size: 1.8rem;
            margin-right: 2rem;
            &:last-of-type{
                margin-right: 0;
            }  
        }
        &::hover{
            color: v.$verde;
        }
    }
}

// Menu de hamburguesa

.mobile-menu{
    @include m.tablet {
        display:none;
    }
    img{
        width: 5rem;
    }
}

.mostrar{
    // display: flex;
    // flex-direction: column;
    // @include m.tablet {
    //     flex-direction: row;
        
    // }
    visibility: visible;
    opacity: 1;
    height: auto;
}
