@use 'base/variables' as v; 



.alerta{
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    margin: .5rem 0;
    &.error{
        color: v.$rojoError;
    }
    &.exito{
        color: v.$verde;
    }
}

table.propiedades{
    text-align: center;
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    a{
        margin-top: 3rem;
    }
    thead{
        background-color: v.$verde;

        th{
            color: v.$blanco;
            padding: 2rem;
        }
    }


    .imagen-tabla{
        width: 10rem;
        margin: 0 auto;
    }
}

.imagen-formulario{
 width: 20rem;
}