@use 'base/mixins' as m; 
@use 'base/variables' as v; 

.formulario{
    display: block;

    p{
        font-size: 1.4rem;
        color:v.$grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend{
        font-size: 2rem;
        color: v.$grisOscuro;
    }

    label{
        font-weight: bold;
        text-transform: uppercase;
        display: block;

    }
    input:not([type="submit"]) ,
    textarea, 
    select{
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    .forma-contacto{
        max-width: 12% ;
        display: flex;
        justify-content: space-between;
        align-items: center;
        
     
    }
    input[type="radio"]{
        margin: 0;
        width: auto;
    }

    .espacio{
        margin-right: 1rem;
        margin-left: 1rem;
    }

}

