@use 'base/variables' as v; 
@use 'base/mixins' as m; 

@include m.tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 1rem;
    @include m.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }
    img{
        margin-bottom: 1rem;
    }
}
.texto-entrada{
    a{
        color: v.$negro;
        @include m.tablet {
            margin-top: 0;
            
        }
        
    }
    h4,h2,h3{
        margin: -8px;
        line-height: 1.5;
        &::after{
            content: "";
            display: block;
            width: 50%;
            height: .5rem;
            background-color: v.$verde;
            margin-top: 1rem;
        }
        

    }

}
.informacion-span{
    
    span{
        color: v.$amarillo;
  
    }

}
