@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.no-web .inicio{
    background-image: url(../img/header.jpg);
}
.webp .inicio{
    background-image: url(../img/header.webp);
}
.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio{
        background-position: center center;
        background-size: cover;
        @include m.tablet {
            min-height: 70rem;
        }
        .contenido-header{
            @include m.tablet {
                min-height:70rem;
            }
        }
    }
}
.contenido-header{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include m.tablet {
        .contenido-header{
            text-align: left;
        }
    }

    h1{
        color:v.$blanco;
        padding-bottom: 2rem;
        max-width: 60rem;
        line-height: 2;
        text-align: left;
        font-weight: bold;
    }

 
    
}
.mobile-menu{
    &:hover{
        cursor: pointer;
    }
}



