@use 'base/variables' as v; 
@use 'base/mixins' as m; 

@include m.tablet{
    .contenedor-anuncios{
        display: grid;
        grid-template-columns: repeat(3, 33.3%);
        gap: 2rem;
        align-items: flex-start;
        word-wrap: break-word;
        
    }
    
}



.anuncio{
    background-color:lighten(v.$gris, 5);
    margin-bottom: 1rem;
    height: 100%;

}

.contenido-anuncio{
    padding: 2rem;
    h3,p{
        margin: 0;
    }    
}
.precio{
    color: v.$verde;
    font-weight: bold;
    font-size: 3rem;
    }

.iconos-caracteristicas{
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    flex: 1;
    max-width: 40rem;
    margin: 1rem auto;
}
 .iconos-caracteristicas li{
        display: flex;
        align-items: center;


        img{
            margin-right: 2rem;        
        }
        p{
            font-weight: v.$black;
        }
        
    }



.resumen-propiedad{
    
    letter-spacing: .1rem;
    word-spacing: .2rem;
    .resumen-descripcion{
        padding:0 1rem;
    }
    p{
        margin: 1rem 0;
        text-align: justify;
    }
}
.anuncio .boton {
    width: 100%;
    text-transform: uppercase;

    &:hover {
        background-color: darken(v.$amarillo, 5);
        cursor: pointer;
    }
}



