@use 'base/variables' as v; 

.footer{


    padding: (v.$separacion,2,0);
    background-color: v.$grisOscuro;

    .contenedor-footer{
        padding-top: (v.$separacion,2);
      
        .navegacion{
            visibility:visible;
            opacity: 1;
            height: auto;
        }
    }

    .copyright{
        color: v.$blanco;
        text-align: center;
        margin: 0;
    }
    p{
        margin:0;
    }
    margin-top: 2.7rem;
    height: auto;
   

}